<template>
  <div id="app">
    <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
     <!-- 导航栏 -->
    <div v-if="!$route.meta.hideNav" class="nav-container">
      <div class="header-section">
        <h1 class="title">模·拟·面·试</h1>
        <span class="designer">欢迎{{ username }}</span>
      </div>
      <nav class="nav-menu">
        <a href="#" class="nav-item" :class="{ 'active': activeIndex === 'home' }" @click.prevent="handleSelect('home')">首页</a>
        <a href="#" class="nav-item" :class="{ 'active': activeIndex === 'interview' }" @click.prevent="handleSelect('interview')">模拟面试</a>
        <a href="#" class="nav-item" :class="{ 'active': activeIndex === 'personal' }" @click.prevent="handleSelect('personal')">个人主页</a>
        <a href="#" class="nav-item" :class="{ 'active': activeIndex === 'friends' }" @click.prevent="handleSelect('friends')">好友</a>
        <a href="#" class="nav-item" :class="{ 'active': activeIndex === 'about' }" @click.prevent="handleSelect('about')">关于我们</a>
        <a href="#" class="nav-item" :class="{ 'active': activeIndex === 'exit' }" @click.prevent="handleSelect('exit')">退出</a>
      </nav>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
import avatarComponent from './views/avatardemo.vue'


export default {
  name: 'App',
  data() {
    return {
      activeIndex: 'home',
      username: ''
    }
  },
  methods: {
    handleSelect(index) {
      this.activeIndex = index;
      console.log('当前选中：', index);
      if (index === 'home') {
        this.$router.push('/home');
      } else if (index === 'interview') {
        this.$router.push('/interview');
      } else if (index === 'personal') {
        this.$router.push('/personal');
      } else if (index === 'friends') {
        this.$router.push('/friends');
      } else if (index === 'about') {
        this.$router.push('/about');
      } else if (index === 'exit') {
        localStorage.removeItem('token');
        this.$router.push('/login');
      }
    },
    fetchUsername() {
      this.username = localStorage.getItem('username') || '用户';
    }
    
  
  },
  mounted() {
    // 组件挂载时获取用户名
    this.fetchUsername();
    // 监听路由变化，每次跳转后获取用户名
    this.routeListener = this.$router.afterEach(this.fetchUsername);
  },
  beforeDestroy() {
    // 组件销毁前移除路由监听，避免内存泄漏
    if (this.routeListener) {
      this.routeListener();
    }
  },
  components: {
    avatarComponent
  }
}
</script>


