  top: 0;
  z-index: 100;
}

.header-section {
  text-align: center;
  margin-bottom: 20px;
}

.title {
  font-size: 28px;
  color: #4CAF50;
  margin: 0;
  font-weight: 600;
}

.designer {
  font-size: 14px;
  color: #6c757d;
  margin-top: 5px;
  display: block;
}

.nav-menu {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-bottom: 20px;
  border-bottom: 1px solid #ddd;
  padding-bottom: 10px;
}

.nav-item {
  padding: 12px 22px;
  border-radius: 8px;
  font-size: 16px;
  color: #333;
  text-decoration: none;
  transition: all 0.3s ease;
  background: #f5f7fa;
}

.nav-item.active {
  background: #409eff;
  color: white;
}

.nav-item:hover:not(.active) {
